import Carousel from 'react-bootstrap/Carousel';
import '../style/HomeCarousel.css';

function HomeCarousel() {

    return (
        <>
            <Carousel>
                <Carousel.Item>
                    <img
                        className="d-block w-100 slider-img"
                        src="./images/slider1.jpg"
                        alt="First slide"
                    />
                    <Carousel.Caption className="caption-wrapper silder1">
                        <h3>Style Personnalis, Vue Optimale</h3>
                        <p>
                            Chez Safety Vision, nous croyons que votre vision mrite un style personnalis. Dcouvrez
                            notre slection de montures esthtiques et de lentilles mdicales pour une vue optimale qui
                            reflte votre mode de vie
                        </p>

                    </Carousel.Caption>
                </Carousel.Item>
                <Carousel.Item>
                    <img
                        className="d-block w-100 slider-img "
                        src="./images/slider2.jpeg"
                        alt="Second slide"
                        style={{objectFit: "cover"}}
                    />
                    <Carousel.Caption className="caption-wrapper slider2">

                        <h3>Clart et lgance au Quotidien</h3>
                        <p>
                            Dcouvrez chez Safety Vision une vision claire et lgante au quotidien avec nos lunettes
                            mdicales et solaires polarises. Optez pour le style sans compromis sur la qualit
                        </p>

                    </Carousel.Caption>
                </Carousel.Item>
                <Carousel.Item>
                    <img
                        className="d-block w-100 slider-img "
                        src="./images/slider2.jpg"
                        alt="Third slide"
                    />
                    <Carousel.Caption className="caption-wrapper slider3">

                        <h3>Conseils Personnaliss, Votre Vision, Notre Priorit</h3>
                        <p>
                            Chez Safety Vision, nos experts en optique vous offrent des conseils personnaliss pour
                            trouver les lunettes parfaites adaptes  votre style de vie. Faites confiance  notre
                            engagement envers votre vision, o qualit et style se rencontrent harmonieusement
                        </p>
                    </Carousel.Caption>
                </Carousel.Item>
            </Carousel>
        </>
    );
}

export default HomeCarousel;